.square-wheel{position:relative;display:block;height:70px;width:70px;background-color:#eeeeee;border-radius:5px;-webkit-animation:spin 4s ease infinite;animation:spin 4s ease infinite}.square-wheel::before{content:"";display:block;position:relative;top:17px;left:17px;height:35px;width:35px;background-color:#1abc9c;-webkit-animation:spin 2s ease infinite;animation:spin 2s ease infinite}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
